import { getBengzhan} from '../services/Formurl';
export default {
  namespace: 'bengzhanspace',
  state: {
    bengzhanData: 0,
    opend: '',
  },

  effects: {
    *getBengzhan({ payload }, { call, put }) {
      let bengzhanData;
      const response = yield call(getBengzhan,payload);
      console.log(response);
      bengzhanData = response.result;
      yield put({
        type: 'ztbengzhanData',
        payload: bengzhanData,
      });
    },
  },
  reducers: {
    ztbengzhanData(state, { payload }) {
      return { ...state,pumpingstation: payload };
    },
  },
  setOpened(state) {
    return {
      ...state,
      opend: true,
    }
  }
};
